import { add as add使, mul as mul使 } from '../math'
import { reduce } from './iter'

export const
  sum     = reduce(add使)(0),
  product = reduce(mul使)(0)

export const
  add = (b: number) => (a: number) => a + b,
  sub = (b: number) => (a: number) => a - b,
  mul = (b: number) => (a: number) => a * b,
  div = (b: number) => (a: number) => a / b
